<template>
  <div id="calc" class="round2">
    <h3>Узнайте стоимость</h3>
    <div id="divtariff">
      <label for="tariff">Тариф</label>
      <select id="tariff" v-model="tariff">
        <option value="avia" selected>Авиадоставка</option>
        <option value="auto">Автодоставка</option>
        <option value="train">Доставка поездом</option>
      </select>
    </div>
    <CalcIn wcost="true" :tariff="tariff" @values="values = $event"/>
    <div class="form">
      <small>
        <a :href="refParams">Подробный расчет</a>
      </small>
     </div>
  </div>
</template>

<script>
import CalcIn from '../components/CalcIn.vue'

export default {
  name: 'SmallCalc',
  components: {
    CalcIn
  },
  data(){ return {
    tariff: 'avia',
    values: {},
  }},
  computed: {
    refParams: function(){
      let href = new URL(window.location + 'tariffs')
      let values = this.values
      delete values.result
      delete values.apiece
      values.tariff=this.tariff
      href.search = new URLSearchParams(values)
      return href;
    }
  }
}
</script>

<style scoped>
#calc {
  width: 260px;
  flex-shrink: 0;
}
select {
  width: 100%;
}
#divtariff {
  margin: 0 10px;
}
</style>