<template>
  <div id="contentcard" :class="{reversed: card.id % 2}" v-scroll="visible">
    <div class="image" :style="card.bstyle">
      <div class="pattern" v-scroll="parallax">
        <img :src="card.imgb">
      </div>
      <img :src="card.imgf" class="foreground">
    </div>
    <p>{{ card.text }}</p>
  </div>
</template>

<script>
export default {
  name: 'ContentCard',
  props: ['card'],
  methods: {
    parallax (evt, el){
      el.setAttribute("style", "background-position-y: "+window.scrollY*0.4+"px;")
    },
    visible (evt, el){
      let rect = el.getBoundingClientRect()
      if (rect.bottom < window.innerHeight){
        el.classList.add('visible')
      }
    },
  }
}
</script>

<style scoped>

#contentcard {
  display: flex;
  align-items: center;
  margin: 40px 0;
}

.reversed { flex-direction: row-reverse; }

p { max-width: 250px; }

.image {
  height: 150px;
  width: 250px;
  border-radius: 6px;
  margin: 0 40px;

}
.pattern {
  background-image: url('../assets/bg2.png');
  isolation: isolate;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

img {
  object-fit: contain;
  max-width: 250px;
  max-height: 150px;

  transform: translateX(50px) scale(1.1);
  opacity: 0;
  
  transition: transform 1s ease-out,  opacity 1s ease-out;
}
.reversed img {
  transform: translateX(-50px) scale(1.1);
}
.visible img {
  transform: translateX(0) scale(1.1);
  opacity: 1;
}

.foreground {
  position: relative;
  top: -150px;
}
@media screen and (max-width: 600px) {
  #contentcard {
    flex-direction: column;
    height: auto;
  }
  
  img {
    width: 100%;
    max-height: 250px;
  }
}
</style>